import { DropdownItem } from "./MultilevelMenu";
import { FaBell } from "react-icons/fa";
import { useIdentity } from "../hooks/identity";
import { useState, useEffect } from "react";

export function PushSubToggle() {
    var identity = useIdentity();
    const [subs, setSubs] = useState([]);

    useEffect(() => {
        setSubs(JSON.parse(localStorage.getItem("notifications")));
    }, []);

    function persist() {
        localStorage.setItem("notifications", JSON.stringify(subs));
    }

    function onClick() {

    }

    return (
    <DropdownItem 
        onClick={onClick}
        leftIcon={<FaBell />} 
        text={<>Notifications: <span style={{ color: "red" }}>OFF</span></>}
    ></DropdownItem>);
}

